@import url(//fonts.googleapis.com/css?family=Open+Sans);

.App {
  text-align: center;
}

mark {
  background-color: #111317;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  font-style: italic;
  color: #a3a3a3;
  font-weight: bold;
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  font-style: italic;
  color: #a3a3a3;
  font-weight: bold;
}

::-ms-input-placeholder { /* Microsoft Edge */
  font-style: italic;
  color: #a3a3a3;
  font-weight: bold;
}

label.Label {
  font-family: 'Open Sans', sans-serif;
  display: flex;
  flex-direction: column;
    align-items: center;
  min-width: 500px;
  justify-content: center;
  font-size: 18px;
  margin-top: 8px;
}

.LoginText {
  margin-top: 8px;
  border: none;
  color: #f5f5f5;
  font-weight: bold;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  padding: 10px;
  background-color: #111317;
  width: 100%;
}

mark.newUser {
  color: #78c977;
  font-weight: bold;
  font-style: italic;
}

mark.userLeft {
  color: #c97777;
  font-weight: bold;
  font-style: italic;
}

.Messages {
  float: left;
  width: 70%;
}

.Users {
  float: left;
  width: 30%
}

.MessageText {
  float: left;
  border: none;
  color: #f5f5f5;
  font-weight: bold;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  padding: 10px;
  width: 75%;
  margin-bottom: 25px;
background-color: #111317;  
}

.LoginButton {
  width: 15%;
  float: right;
  background-color: #1a1e24;
  color: #f5f5f5;
  padding: 10px;
  margin-right: 10px;
  font-size: 14px;
  font-weight: bold;
  border: none;
  font-family: 'Open Sans', sans-serif;
}

.MessageButton {
  width: 15%;
  float: right;
  background-color: #1a1e24;
  color: #f5f5f5;
  padding: 10px;
  margin-right: 10px;
  font-size: 14px;
  font-weight: bold; 
  border: none;
  font-family: 'Open Sans', sans-serif;
}

mark.serverUpdateMessage {
  font-style: italic;
  color: #a3a3a3;
}

mark.user {
  color: #77aac9;
  font-weight: bold;
}

mark.date {
  color: #a3a3a3;
}

mark.newUserMessage {
  color: #f5f5f5;
  font-weight: bold;
  font-style: italic;
}

mark.message {
  color: #f5f5f5;
  font-weight: bold;
}

ul {
  font-family: 'Open Sans', sans-serif;
  text-align:left;
  font-size: 14px;
}

ul.users {
  font-weight: bold;
}

ul.messages {
  min-width: 30vw;
  background-color: #111317;
  padding: 30px;
  font-size: 14px;
  overflow-y: scroll;
  display: block;
  max-height: 60vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.MainPage {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  max-width: 100%
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
